<template>
  <main class="bg-light">
    <div class="container">
      <h1 class="font-weight-bold text-center text-uppercase h2 p-3">Аналоговое видеонаблюдение: AHD, CVI, TVI форматы</h1>
      <p class="p-2">Сегодня мало какой объект бизнеса обходится без видеонаблюдения, и часто встает вопрос, какую камеру выбрать. В этой статье рассмотрим новые форматы аналоговых видеокамер. На сегодняшний день используют 3 вида: HD-TVI, HD-CVI и AHD.  Разрабатывались они для создания аналогового видеонаблюдения с высокой точностью, способной конкурировать с цифровыми видеокамерами. Разработчикам удалось создать аналоговую видеосистему высокого качества, которая позволяет полностью обновить старую, не заменяя кабель. Преимущества получили и новые пользователи. У них есть возможность установить видеонаблюдение высокой четкости по более выгодной цене.</p>
      <h2 class="font-weight-bold text-center text-uppercase h3 p-3">Отличия AHD, CVI, TVI форматов</h2>
      <p class="p-2">Рассмотрим отличия каждого из форматов.</p>
      <p class="p-2">AHD – это аналоговая система видеонаблюдения высокой четкости. Она сочетает в себе цифровые и аналоговые технологии. AHD-камера обрабатывает сигнал в цифровом виде, после чего он преобразовывается в аналоговый. Подобный симбиоз позволяет получить изображение высокого качества, передать его на большую дистанцию (без потери этого качества) и при этом сохранить низкую цену.</p>
      <p class="p-2">Стандарт HD-TVI (или просто TVI) является новым стандартом в области аналогового видеонаблюдения и позволяет передавать видеосигнал с разрешением 720, 1080 и до 4000 пикселей.</p>
      <p class="p-2">Основное отличие нового стандарта заключается в измененной технологии формирования и передачи видеосигнала. Стандарт предполагает полное разделение сигнала яркости и цветности, что позволяет избежать накопления помех для получения более чёткого изображения.</p>
      <p class="p-2">Стандарт HD-CVI расшифровывается как «композитный видеоинтерфейс высокого разрешения», аналоговая технология, основанная на передаче видеосигнала по коаксиальному кабелю. Это закрытый стандарт и контролируется компанией Dahua. Другие компании могут выпускать оборудование стандарта CVI лишь покупая CVI чипсеты у Dahua и не могут их сами производить. В связи с чем им сложно конкурировать по цене с Dahua. И поэтому поддержка стандарта CVI у других производителей мало распространена.</p>
      <p class="p-2">Видеорегистраторы при этом может распознавать сигнал со всех видов аналоговых камер: HD-TVI, HD-CVI, AHD, а также с традиционных аналоговых камер низкого разрешения, и даже с IP камер.</p>
      <h2 class="font-weight-bold text-center text-uppercase h3 p-3">Питание по коаксиальному кабелю.</h2>
      <p class="p-2">Одним из преимуществ IP-камер был Power over Ethernet (PoE), который позволяет использовать вместо нескольких кабелей для видео и для питания, один сетевой кабель и получать питание от маршрутизатора.</p>
      <p class="p-2">Аналогичная функция Power over Coax для аналогового HD позволила использовать только коаксиальный кабель, отключить внешние источники питания видеокамер и сократить время монтажа.</p>
      <dl>
        <dt>POC (Power over Coax)</dt>
        <dd>Технология передачи напряжения питания по коаксиальному кабелю одновременно с передачей видеопотока.</dd>
      </dl>
      <dl>
        <dt>СOC (Control over Coax)</dt>
        <dd>Технология управления видеокамерами через обычный коаксиальный кабель.</dd>
      </dl>
      <p class="p-2">Совмещение технологий POC/COC позволяет использовать только один кабель и тем самым сэкономить средства и ресурсы.</p>
      <h2 class="font-weight-bold text-center text-uppercase h4 p-3">Преимущества аналоговых систем:</h2>
